import loginConstants from '../constants/LoginConstants';
import loginServices from '../services/LoginServices';
import { push } from 'connected-react-router'
import { Trans } from 'react-i18next'
import React from 'react';
import { notify } from '../../helpers/notifications'

export default {
    login,
    signup,
    logout    
};

function logout() {
    return dispatch => {
        localStorage.removeItem("session")
        dispatch(logout()); dispatch(push('/'))        
    };
    function logout() { return { type: loginConstants.LOGIN_LOGOUT } }
}

function login(email, password) {
    return dispatch => {
        dispatch(request());
        loginServices.login(email, password)
            .then(
                r => {
                    if (r.success) {                                  
                        localStorage.setItem('session', JSON.stringify(r.session))
                        notify(<Trans i18nKey='login.signIn.Welcome' />, () => {
                            dispatch(success(r.session)); dispatch(push('/'))
                        })
                    } else {
                        notify(<Trans i18nKey='login.signIn.WrongCredentials' />, () => {
                            dispatch(failure())
                        })
                    }
                },

                e => { dispatch(failure()) }
            );
    };

    function request() { return { type: loginConstants.LOGIN_REQUEST } }
    function success(session) { return { type: loginConstants.LOGIN_SUCCESS, session } }
    function failure() { return { type: loginConstants.LOGIN_FAILURE } }
}

function signup(data) {
    return dispatch => {
        dispatch(request());
        loginServices.signup(data)
            .then(
                r => {
                    if (r.success == true) {                        
                        notify(<Trans i18nKey='login.signUp.Success' />, () => {
                            dispatch(success()); dispatch(push('/'))
                        })                       
                    } else {
                        notify(<Trans i18nKey='common.forms.RepetedEmail' />, () => {
                            dispatch(failure())
                        })
                    }
                },
                e => { dispatch(failure()) }
            );
    };

    function request() { return { type: loginConstants.SIGNUP_REQUEST } }
    function success() { return { type: loginConstants.SIGNUP_SUCCESS } }
    function failure() { return { type: loginConstants.SIGNUP_FAILURE } }
}


