
import React from 'react';
import Header from '../header/Header'
import SideNav from '../sidenav/SideNav'

class Base extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      visible: false
    }

    this.togleSideNav = this.togleSideNav.bind(this)
  }

  togleSideNav() {
    this.setState({ visible: !this.state.visible })
  }

  render() {
    return (
      <div className="bg-geyser">
        <Header onToggleSideNav={this.togleSideNav} />
        <SideNav onToggleSideNav={this.togleSideNav} {...this.props} {...this.state}></SideNav>
        <section>
          {this.props.children}
        </section>
      </div>
    );
  }
}

export default Base;






