import axios from 'axios';

export default {
    signup,
    login
};

function signup(data) {

    let bodyFormData = new FormData();

    bodyFormData.set('signup_nombre', data.signup_nombre);
    bodyFormData.set('signup_email', data.signup_email);
    bodyFormData.set('signup_password', data.signup_password);
    bodyFormData.set('signup_password_repeate', data.signup_password_repeate);

    return axios.post(process.env.REACT_APP_DEV_SERVICE_SIGNUP, bodyFormData)
        .then(res => {
            if (res.status === 200) {
                return Promise.resolve(res.data)
            }
        })
        .catch(function () {
            return Promise.reject();
        });
}


function login(email, password) {

    let bodyFormData = new FormData();
    bodyFormData.set('email', email);
    bodyFormData.set('password', password);

    return axios.post(process.env.REACT_APP_DEV_SERVICE_LOGIN, bodyFormData)
        .then(res => {
            if (res.status === 200) {
                return Promise.resolve(res.data);
            }
        })
        .catch(function () {
            return Promise.reject();
        });
}



