import { toast } from 'react-toastify';

var toastId = null
const id = 'xxx-yyy';

export function notify(msg,func){
    if (toastId == null) {
        toastId = toast.success(msg, {
            position: toast.POSITION.BOTTOM_CENTER,   
            className: 'bg-dark',             
            toastId: id,
            closeButton: false,
            onClose: () => { toastId = null; if(func) func() }
        });
    }
}



