
import React from 'react';
// import PropTypes from 'prop-types';
// import { Link } from 'react-router-dom';

// import { translate } from 'react-i18next';
import loginActions from '../../../redux/actions/LoginActions'
import profileImg from '../../../assets/img/profile-jp.jpg'
import talents from '../../../assets/img/tln-3.png'
import ideal from '../../../assets/img/idc-1.png'
import ask from '../../../assets/img/h-1.png'
import answer from '../../../assets/img/h-2.png'
import './SideNav.scss'

class SideNav extends React.Component {

    constructor(props) {
        super(props);

        this.logOut = this.logOut.bind(this)
    }

    logOut() {
        const { dispatch } = this.props
        dispatch(loginActions.logout())
    }

    render() {
        let { t, session } = this.props;

        // {/* <script>
        //         window.dataLayer = window.dataLayer || [];
        //         function gtag(){dataLayer.push(arguments);}
        //         gtag('js', new Date());

        //         gtag('config', 'UA-121257404-1');
        //       </script> */
        const overlayClass = this.props.visible ? "overlay active" : "overlay";
        const sidebarClass = this.props.visible ? "sidebar active" : "sidebar";
        const usrImg = session.photo ? "uploads/" + session.photo : profileImg;

        return (
            <div>
                <nav className={sidebarClass}>
                    <ul className="list-unstyled">
                        <li>
                            <div className="user-view d-flex align-items-center flex-column p-4">
                                <a href="#user"><img className="rounded-circle img-fluid" src={usrImg} /></a>
                                <span className="name text-cutty-sark ssprsb14 mt-2">{session.name}</span>
                                <span className="email text-cutty-sark ssprsb14 d-flex align-items-center mt-2">
                                    {/* arturo.avbraca@gmail.com */}
                                    {session.email}
                                    <i className="material-icons ml-2">create</i>
                                </span>
                            </div>
                        </li>
                        <li className="border-bottom p-4 d-flex align-items-center justify-content-center">{t('base.Wallet')}</li>
                        <li className="container px-4 py-5">
                            <div className="row coins">
                                <div className="col-6 d-flex align-items-center flex-column">
                                    <img className="rounded-circle" src={talents} alt="talents" />
                                    <span id="refresh_tln">0 TLN</span>
                                </div>
                                <div className="col-6 d-flex align-items-center flex-column">
                                    <img className="rounded-circle" src={ideal} alt="ideal" />
                                    <span id="refresh_tln">0 IDC</span>
                                </div>
                            </div>
                        </li>
                        <li className="container p-4 waves-effect">
                            <div className="d-flex justify-content-center position-relative align-items-center shadow custom-border pointer">
                                <img className="img-fluid" src={ask} alt="ask" />
                                <div className="position-absolute">
                                    <div className="ssprb65 text-limed-spruce">{t('common.ask.Title')}</div>
                                    <div className="ssprsb14 text-limed-spruce text-center">{t('common.ask.Desc1')} <br /> {t('common.ask.Desc2')}</div>
                                </div>
                            </div>
                        </li>
                        <li className="container p-4 waves-effect">
                            <div className="d-flex justify-content-center position-relative align-items-center shadow custom-border pointer">
                                <img className="img-fluid" src={answer} alt="ask" />
                                <div className="position-absolute">
                                    <div className="ssprb65 text-limed-spruce">{t('common.answer.Title')}</div>
                                    <div className="ssprsb14 text-limed-spruce text-center">{t('common.answer.Desc1')} <br /> {t('common.answer.Desc2')}</div>
                                </div>
                            </div>
                        </li>
                        <li className="container p-4">
                            <a onClick={this.logOut} className="waves-effect waves-light text-center text-white sign-off bg-promegranate pointer">{t('base.LogOut')}</a>
                        </li>
                    </ul>
                </nav>
                <div className={overlayClass} onClick={this.props.onToggleSideNav}></div>
            </div>
        );
    }
}

export default SideNav;


