
import React from 'react';
// import { Link } from 'react-router-dom';
import logo from '../../../assets/img/logo-gray-1.png'
import './Header.scss'

class Header extends React.Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <nav className="fixed-top shadow py-2 px-4 bg-yellow d-flex align-items-center">
        <a href="" className="logo d-flex"><img className="responsive-img" src={logo} alt="logo" /></a>
        <i onClick={this.props.onToggleSideNav} className="material-icons ml-auto text-white">menu</i>
      </nav>
    );
  }
}

export default Header;






